div.homeContainer {
  display: flex;
  width: 100%;
  flex-direction: column;
}

div.homeContainer .quickLinks {
  position: fixed;
  top: 70%;
  left: 95%;
}

div.homeContainer .section {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  align-self: center;
  justify-content: center;
}

div.homeContainer .section .sectionContainer {
  display: flex;
  height: 87%;
  width: 95%;
  align-items: center;
  align-self: center;
  overflow: auto;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #122f5947;
  margin-top: 4em;
  backdrop-filter: blur(10px);
}

div.homeContainer .section .sectionContainer .sectionHeading {
  display: flex;
  align-self: flex-start;
  color: white;
  margin: 0.3em;
  font-family: monospace;
  font-weight: 600;
}

div.section .informationCardContainer {
  display: flex;
  width: 90%;
  height: fit-content;
  justify-content: space-between;
  flex-direction: column;
  margin: 1em;
  border-radius: 0.7em;
}

div.section .informationCardContainer .informationCard {
  display: flex;
  height: fit-content;
  flex-flow: column;
  width: 100%;
  border-radius: 0.7em;
}

div.section .informationCardContainer .informationCard .informationCardTitle {
  display: flex;
  background-color: #1b1b1b;
  color: white;
  box-shadow:
    0em 0.2em 0.2em -0.1em rgba(0, 0, 0, 0.2),
    0em 0.3em 0.4em 0.5em rgba(0, 0, 0, 0.14),
    0em 0.1em 0.4em 0.4em rgba(0, 0, 0, 0.12);
  border-radius: 0.5em;
}

div.section
  .informationCardContainer
  .informationCard
  .informationCardTitle.active {
  border-bottom-left-radius: 0em;
  border-bottom-right-radius: 0em;
}

div.section
  .informationCardContainer
  .informationCard
  .informationCardTitle
  svg {
  color: white;
}

div.section .informationCardContainer .informationCard .informationCardDetails {
  display: flex;
  color: white;
  background-color: #1b1b1b;
  opacity: 0.8;
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  flex-direction: column;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  div.cardHighlights {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  div.cardHighlights
  > div {
  display: flex;
  flex-direction: row;
  font-style: italic;
  height: 3em;
  align-items: center;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  div.cardHighlights
  > div
  > h6,
.informationCardContainer
  .informationCard
  .informationCardDetails
  div.cardHighlights
  > div
  > p {
  display: flex;
  font-weight: 800;
  align-self: center;
  margin: 0.3em;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  .bulletPoint {
  width: 0.4m;
  height: 0.4em;
  align-self: center;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  div.cardHighlights
  img {
  border-radius: 0.5em;
}

.informationCardContainer .informationCard .informationCardDetails .gpa {
  margin-left: 2em;
  font-weight: 600;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  .githubButton {
  border-radius: 1.5em;
  margin-left: 2em;
  background-color: black;
}

.informationCardContainer
  .informationCard
  .informationCardDetails
  .githubButton:hover {
  background-color: var(--secondary-color);
}

div.section .sectionContainer .publicationCard {
  display: flex;
  align-self: center;
  margin: 1em;
  width: 80%;
  flex-direction: column;
  background-color: #1b1b1b;
  color: white;
  border-radius: 1em;
  box-shadow:
    0em 0.2em 0.2em -0.1em rgba(0, 0, 0, 0.2),
    0em 0.3em 0.4em 0.5em rgba(0, 0, 0, 0.14),
    0em 0.1em 0.4em 0.4em rgba(0, 0, 0, 0.12);
}

div.section .sectionContainer .publicationCard .publicationCardHeader span {
  color: white;
}

div.section .sectionContainer .publicationCard .publicationCardContent {
  background-color: #525252;
  padding: 1.5em;
}

div.section
  .sectionContainer
  .publicationCard
  .publicationCardContent
  .subHeading2 {
  display: flex;
  flex-direction: row;
  align-items: center;
}

div.section .sectionContainer .publicationCard .publicationCardContent > div {
  display: flex;
  align-items: center;
}

div.section
  .sectionContainer
  .publicationCard
  .publicationCardContent
  .subHeading2
  > div {
  display: flex;
  align-items: center;
  margin-right: 1em;
}
div.section .sectionContainer .publicationCard .publicationCardContent p {
  font-weight: 600;
  margin-right: 0.5em;
}

div.section
  .sectionContainer
  .publicationCard
  .publicationCardContent
  .subHeading2
  img {
  max-width: 5em;
}

div.section .sectionContainer .imageContainer {
  display: flex;
  width: 80%;
  align-content: center;
}

div.section .sectionContainer .imageContainer img {
  cursor: pointer;
  border-radius: 1em;
}

div.section .sectionContainer .imageContainer .imageTitle {
  opacity: 0;
}

div.section .sectionContainer .imageContainer .imageTitle:hover {
  opacity: 1;
}

@media screen and (max-width: 900px) {
  div.section h3 {
    font-size: x-large;
  }

  div.section h5 {
    font-size: medium;
  }

  div.section h6 {
    font-size: small;
    white-space: nowrap;
  }

  div.section p {
    font-size: x-small;
  }

  div.section .informationCardContainer div {
    font-size: x-small;
  }

  div.section ul li {
    font-size: x-small;
  }

  div.section .sectionContainer .publicationCard .publicationCardHeader span {
    font-size: small;
  }

  div.section .sectionContainer .publicationCard .publicationCardContent {
    font-size: x-small;
    overflow-y: auto;
  }

  div.section .sectionContainer .imageContainer > ul {
    column-count: 2 !important;
    column-gap: 0.5em !important;
  }
}
